.header {
  display: flex;
}
.socials {
  display: flex;
  margin-top: 16px;
  margin-right: 19px;
  margin-left: auto;

  &__item:nth-child(-n + 2) {
    margin-right: 20px;
  }

  &__image {
    height: 20px;
    width: 20px;
  }
  @media screen and (min-width: 1200px) {
    margin-top: 18px;
    margin-right: 35px;

    &__item:nth-child(-n + 2) {
      margin-right: 30px;
    }
  }
}
.phone {
  display: flex;
  flex-direction: column;
  text-align: center;
  font-weight: 500;
  font-size: 10px;
  margin: 10 0 0 0;
  @media screen and (min-width: 1200px) {
    margin: 10 25 0 0;
    font-size: 14px;
  }
  &__number {
    font-size: 15px;
    @media screen and (min-width: 1200px) {
      font-size: 22px;
    }
  }
}
